.tag-box {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    margin-top: 30px;
    width: 100%;
    color: white; 
    height: 25px;
}
   

.tag {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-right: 15px;
    padding: 15px;
    margin-top: 15px;
    width: fit-content;
    border-radius: 10px;
    background-color: #ff6060;
    color: white;
}

@media (max-width: 820px)  {
    .tag {
        /* width: 60px;
        height: 18px; */
        font-size: 10px;
        height: 10px;
    }

    .tag-box {
        margin-top: 10px;
        height: 18px;
    }

}
