.hosts {

    font-size: 18px;
    font-weight: 500;
    line-height: 25.67px;
    margin-top: 20px;
    color: #FF6060;
    
}

.hosts section{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    width: 140px;
    
}

.hosts img {
  border-radius: 50%;
  width: 64px;
  height: 64px;
}

.hosts h1 {
  
  line-height: normal;

}

@media (max-width: 820px) {
  .hosts {
    font-size: 12px;
    
}

  .hosts img {
    border-radius: 50%;
    width: 32px;
    height: 32px;
  }

  .hosts section{
    width: 80px;
    justify-content: space-around;
  }
}