.collapse p {
    height: auto;
    opacity: 1;
    background-color: #f6f6f6;
    transition: all ease-in-out;
    border-bottom-right-radius: 10px;
    color: #ff6060;
    margin-top: -10px;
    padding: 31px 18px 19px 18px;
    
  }
  .collapse ul {
    height: auto;
    opacity: 1;
    background-color: #f6f6f6;
    transition: all ease-in-out;
    border-bottom-right-radius: 15px;
    color: #ff6060;
    list-style-type: none;
    margin-top: -10px;
    padding: 31px 18px 19px 18px;
    
  }
  
  .container {
    height: 52px;
    background-color: #ff6060;
    color: white;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 18px;
    border-radius: 10px;
    margin-bottom: 10px;
  }

  .container img {
    height: 16px;
    width: 30px;
    
  }


  @media (max-width: 820px) {
    .collapse p {
      margin-bottom: 5%;
      height: auto;
        font-size: 12px;
    }

    .collapse ul{
      font-size: 12px;

    }

    .container {
      height: 30px;
      font-size: 13px;
    }  

    .container img {
      height: 9px;
      width: 15px;
      
    }
      
}
  